.FriendList {
  margin: 0 auto;
  text-align: center;
  border-radius: 5px;
  position: relative;
  margin-bottom: 30px;
  padding: 0;
  list-style: none;
}
.item {
  width: 304px;
  height: auto;
  border: 2px solid #969696;
  border-radius: 5px;
  position: relative;
  display: flex;
  align-items: center;
  margin: auto;
  margin-bottom: 10px;
  box-shadow: 5px -5px 5px 0px rgba(0, 0, 0, 0.75);
}
.avatar {
  width: 70px;
  height: 70px;
  border-radius: 5px;
  margin: 10px 20px;
}
.status {
  width: 25px;
  height: 25px;
  background-color: aqua;
  border-radius: 50%;
  margin-left: 20px;
}
.name {
  margin: 0;
  font-size: 24px;
  font-weight: 700;
}
.online {
  composes: status;
  background-color: rgb(0, 255, 0);
}
.offline {
  composes: status;
  background-color: rgb(207, 24, 24);
}
